import React from "react";
import './Contact.css'
import theme_pattern from '../../Assets/theme_pattern.svg'
import mail_icon from '../../Assets/mail_icon.svg'
import location_icon from '../../Assets/location_icon.svg'
import call_icon from '../../Assets/call_icon.svg'

const Contact =()=> {
    const onSubmit = async (event) => {
        event.preventDefault();
        const formData = new FormData(event.target);
    
        formData.append("access_key", "05ad55a0-1c0c-4e19-9f4d-4d3b2f7d9ca7");
    
        const object = Object.fromEntries(formData);
        const json = JSON.stringify(object);
    
        const res = await fetch("https://api.web3forms.com/submit", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Accept: "application/json"
          },
          body: json
        }).then((res) => res.json());
    
        if (res.success) {
          console.log("Success", res);
          alert(res.message);
        }
      };
    
    return (
        <div id="contact" className="contact">
            <div className="contact-title">
                <h1>
                    Get In Touch
                </h1>
                <img src={theme_pattern} alt="" />
            </div>
            <div className="contact-section">
                <div className="contact-left">
                    <h1>Let's Talk</h1>
                    <p> I'm currently available to do new projects</p>
                    <div className="contact-details">
                        <div className="contact-detail"><img src={mail_icon} alt="" /><p>ag55140@gmail.com</p></div>
                        <div className="contact-detail"><img src={call_icon} alt="" /><p>+91 7877167264</p></div>
                        <div className="contact-detail"><img src={location_icon}alt="" /><p>Jaipur,Rajasthan,India</p></div>
                    </div>
                </div>
                <form onSubmit={onSubmit} className="contact-right">
                    <label htmlFor="">Your Name</label>
                    <input type="text" placeholder="Enter your Name" name='name' />
                    <label htmlFor="">Your Email</label>
                    <input type="email"  placeholder="Enter your Email" name="email"/>
                    <label htmlFor="">Write Your Message here</label>
                    <textarea name="message" rows="8" placeholder="Enter your Message"></textarea>
                    <button type="submit" className="contact-submit">SUBMIT</button>
                    
                </form>
            </div>
        </div>
    )
}
export default Contact